<template>
  <div class="wrapper">
    <div @click="getdetails(v.newsId)" class="newTable" v-for="(v,i) in newsList " :key="i">
      <div class="leftImg">
        <img :src="v.image" alt />
      </div>
      <div class="rightContent">
        <h3>{{v.title}}</h3>
        <p>{{v.newsAbstract}}</p>
        <div class="newBottom">
          <div class="readMores">了解详情</div>
          <div class="date">{{v.createTime}}</div>
        </div>
      </div>
    </div>
    <div @click="getmores()" class="getMore">
      <img src="../../assets/img_Industry/load.png" alt />
      <div>加载更多</div>
    </div>
  </div>
</template>

<script>
import { getNews } from "../../api";
export default {
  data() {
    return {
      newsList: [],
      num: 1
    };
  },
  methods: {
    getdetails(i) {
      this.$router.push({ path: "/Newscenter/details", query: { nu: i } });
      localStorage.setItem("type", "1");
    },
    getmores() {
      var num = this.num + 1;
      this.num = num;
      getNews(num, 4, "desc", "id", 1)
        .then(res => {
          window.console.log(res.data.rows);
          this.newsList = this.newsList.concat(res.data.rows);
          window.console.log(this.newsList);
        })
        .catch(err => {
          window.console.log(err);
        });
    }
  },
  mounted() {
    getNews(1, 4, "desc", "id", 1)
      .then(res => {
        window.console.log(res);
        this.newsList = res.data.rows;
      })
      .catch(err => {
        window.console.log(err);
      });
  }
};
</script>

<style lang="less" scoped>
.wrapper {
  width: 1200px;
  margin: 0 auto;
  .newTable {
    height: 250px;
    padding-bottom: 80px;
    border-bottom: 2px solid #6ba933;
    display: flex;
    margin-top: 80px;
    .leftImg {
      margin-right: 31px;
      img {
        width: 399px;
        height: 249px;
      }
    }
    .rightContent {
      padding: 30px 0 33px 0;
      h3 {
        font-size: 30px;
        color: #354536;
        margin-bottom: 18px;
      }
      p {
        font-size: 16px;
        color: #354536;
        margin-bottom: 21px;
      }
      .newBottom {
        display: flex;
        .readMores {
          width: 138px;
          height: 46px;
          font-size: 16px;
          color: #fff;
          line-height: 46px;
          text-align: center;
          margin-right: 12px;
          background: #6ba933;
        }
        .date {
          line-height: 46px;
          font-size: 16px;
          color: #354536;
        }
      }
    }
  }
  .getMore {
    height: 65px;
    width: 1200px;
    background: #6ba933;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    cursor: pointer;
    div {
      font-size: 24px;
      color: #fff;
      margin-left: 17px;
    }
  }
}
</style>